<div><HomeButton></HomeButton><div class="container"><form class="contact-form" ref="form" @submit.prevent="sendEmail"><text class="header">Get in touch!</text><input class="input" v-model="name" type="text" name="name" placeholder="name"><input class="input" v-model="email" type="text" name="email" placeholder="email"><textarea class="input textarea" v-model="message" :rows="5" :cols="30" name="message" placeholder="message"></textarea><input class="input button" type="submit" value="Send" :disabled="disabled" :class="{ disabled }"></form><div class="about-section"><img class="about-img" :src="aboutImgSrc" alt="photo of Lena Abraham"><text class="about-text">Lena Abraham is a recipe developer and food stylist, living and working in New York City since 2010. She'd love to work with you next! </text></div></div></div>